import type { ICommonComponentProps } from '../interface';

export interface IIconButtonProps extends ICommonComponentProps {
  badgeDot?: boolean | { top: number, right: number }
  icon: React.ReactElement;
  /**
   * 按钮是否被选中
   */
  active?: boolean;
  /**
   * 点击回调
   */
  onClick?: (e: React.MouseEvent<HTMLElement>) => void;
  /**
   * 双击点击回调
   */
  onDoubleClick?: (e: React.MouseEvent<HTMLElement>) => void;
}

export interface IBaseIconButtonProps extends IIconButtonProps {
  // id: string;
  size: 'large' | 'normal' | 'small';
}
